import React, { useState, useEffect } from 'react';
import { useApp } from '../../context/AppContext';
import { getUserSubscriptions } from '../../services/subscriptionService';
import { 
  getSubjects,
  getTopics,
  getModulesByTopic,
  getChaptersByModule
} from '../../services/masterDataService';

const StudentQuestionFilters = ({ filters, onFilterChange, onApplyFilters, onClearFilters, questionCount }) => {
  const { token } = useApp();
  const [isExpanded, setIsExpanded] = useState(true);

  const [subscriptions, setSubscriptions] = useState([]);
  const [selectedSubscription, setSelectedSubscription] = useState(null);
  const [subjects, setSubjects] = useState([]);
  const [topics, setTopics] = useState([]);
  const [modules, setModules] = useState([]);
  const [chapters, setChapters] = useState([]);

  // Load subscriptions on mount
  useEffect(() => {
    if (token) {
      loadSubscriptions();
    }
  }, [token]);

  const loadSubscriptions = async () => {
    try {
      const data = await getUserSubscriptions(token);
      console.log('Loaded subscriptions:', data);
      
      // Filter only active and paid subscriptions
      const activeSubscriptions = (data || []).filter(
        sub => sub.subscriptionStatus === 'ACTIVE' && sub.paymentStatus === 'PAID'
      );
      setSubscriptions(activeSubscriptions);
      
      // Auto-select first subscription if available
      if (activeSubscriptions.length > 0 && !selectedSubscription) {
        setSelectedSubscription(activeSubscriptions[0]);
      }
    } catch (error) {
      console.error('Error loading subscriptions:', error);
    }
  };

  // Load subjects when subscription changes
  useEffect(() => {
    if (selectedSubscription) {
      loadSubjectsForSubscription(selectedSubscription);
    } else {
      setSubjects([]);
      setTopics([]);
      setModules([]);
      setChapters([]);
    }
  }, [selectedSubscription]);

  const loadSubjectsForSubscription = async (subscription) => {
    try {
      let data = [];
      
      if (subscription.subscriptionLevel === 'CLASS' && subscription.classId) {
        data = await getSubjects(token, null, null, subscription.classId, null, true);
      } else if (subscription.subscriptionLevel === 'EXAM' && subscription.examId) {
        data = await getSubjects(token, null, null, null, subscription.examId, true);
      } else if (subscription.subscriptionLevel === 'COURSE' && subscription.courseId) {
        data = await getSubjects(token, null, subscription.courseId, null, null, true);
      }
      
      console.log('Loaded subjects for subscription:', data);
      setSubjects(data || []);
      
      // Reset lower levels
      setTopics([]);
      setModules([]);
      setChapters([]);
    } catch (error) {
      console.error('Error loading subjects:', error);
    }
  };

  // Load topics when subject changes
  useEffect(() => {
    if (filters.subjectId) {
      loadTopics(filters.subjectId);
    } else {
      setTopics([]);
      setModules([]);
      setChapters([]);
    }
  }, [filters.subjectId]);

  const loadTopics = async (subjectLinkageId) => {
    try {
      if (!selectedSubscription) return;
      
      let data = [];
      
      // Use the correct parameter based on subscription type
      if (selectedSubscription.subscriptionLevel === 'CLASS') {
        data = await getTopics(token, subjectLinkageId, null, null, true);
      } else if (selectedSubscription.subscriptionLevel === 'EXAM') {
        data = await getTopics(token, null, subjectLinkageId, null, true);
      } else if (selectedSubscription.subscriptionLevel === 'COURSE') {
        data = await getTopics(token, null, null, subjectLinkageId, true);
      }
      
      console.log('Loaded topics:', data);
      setTopics(data || []);
    } catch (error) {
      console.error('Error loading topics:', error);
    }
  };

  // Load modules when topic changes
  useEffect(() => {
    if (filters.topicId) {
      loadModules(filters.topicId);
    } else {
      setModules([]);
      setChapters([]);
    }
  }, [filters.topicId]);

  const loadModules = async (topicId) => {
    try {
      const data = await getModulesByTopic(token, topicId);
      console.log('Loaded modules:', data);
      setModules(data || []);
    } catch (error) {
      console.error('Error loading modules:', error);
    }
  };

  // Load chapters when module changes
  useEffect(() => {
    if (filters.moduleId) {
      loadChapters(filters.moduleId);
    } else {
      setChapters([]);
    }
  }, [filters.moduleId]);

  const loadChapters = async (moduleId) => {
    try {
      const data = await getChaptersByModule(token, moduleId);
      console.log('Loaded chapters:', data);
      setChapters(data || []);
    } catch (error) {
      console.error('Error loading chapters:', error);
    }
  };

  const handleFilterUpdate = (key, value) => {
    onFilterChange({ ...filters, [key]: value, page: 0 }); // Reset to page 0 when filter changes
  };

  const getDifficultyLevels = () => ['EASY', 'MEDIUM', 'HARD'];
  const getQuestionTypes = () => [
    { value: 'MULTIPLE_CHOICE', label: 'Multiple Choice' },
    { value: 'TRUE_FALSE', label: 'True/False' },
    { value: 'FILL_BLANK', label: 'Fill in the Blank' },
    { value: 'ESSAY', label: 'Essay' }
  ];

  return (
    <div style={{
      background: 'white',
      borderRadius: '16px',
      padding: '24px',
      boxShadow: '0 2px 8px rgba(0,0,0,0.1)',
      border: '1px solid #e5e7eb',
      position: 'sticky',
      top: '20px'
    }}>
      {/* Header */}
      <div style={{
        display: 'flex',
        justifyContent: 'space-between',
        alignItems: 'center',
        marginBottom: '20px',
        paddingBottom: '16px',
        borderBottom: '2px solid #e5e7eb'
      }}>
        <h3 style={{
          margin: 0,
          fontSize: '18px',
          fontWeight: '700',
          color: '#1f2937',
          display: 'flex',
          alignItems: 'center',
          gap: '8px'
        }}>
          🔍 Filters
          {questionCount !== undefined && (
            <span style={{
              fontSize: '14px',
              fontWeight: '600',
              color: '#667eea',
              background: '#e0e7ff',
              padding: '4px 12px',
              borderRadius: '12px'
            }}>
              {questionCount}
            </span>
          )}
        </h3>
        <button
          onClick={() => setIsExpanded(!isExpanded)}
          style={{
            background: 'none',
            border: 'none',
            fontSize: '20px',
            cursor: 'pointer',
            padding: '4px'
          }}
        >
          {isExpanded ? '▼' : '▶'}
        </button>
      </div>

      {isExpanded && (
        <div style={{ display: 'flex', flexDirection: 'column', gap: '20px' }}>
          {/* Subscription Selection */}
          <div>
            <label style={{
              display: 'block',
              fontSize: '14px',
              fontWeight: '600',
              color: '#374151',
              marginBottom: '8px'
            }}>
              🎓 My Subscription
            </label>
            <select
              value={selectedSubscription?.subscriptionId || ''}
              onChange={(e) => {
                const subId = parseInt(e.target.value);
                const sub = subscriptions.find(s => s.subscriptionId === subId);
                setSelectedSubscription(sub);
                // Reset all filters
                handleFilterUpdate('subjectId', undefined);
                handleFilterUpdate('topicId', undefined);
                handleFilterUpdate('moduleId', undefined);
                handleFilterUpdate('chapterId', undefined);
              }}
              style={{
                width: '100%',
                padding: '10px 12px',
                borderRadius: '8px',
                border: '2px solid #667eea',
                fontSize: '14px',
                color: '#374151',
                background: 'white',
                cursor: 'pointer',
                fontWeight: '500'
              }}
            >
              {subscriptions.length === 0 && (
                <option value="">No active subscriptions</option>
              )}
              {subscriptions.map(sub => {
                let displayText = sub.subscriptionLevel;
                if (sub.className) displayText += ` - ${sub.className}`;
                else if (sub.examName) displayText += ` - ${sub.examName}`;
                else if (sub.courseName) displayText += ` - ${sub.courseName}`;
                
                return (
                  <option key={sub.subscriptionId} value={sub.subscriptionId}>
                    {displayText}
                  </option>
                );
              })}
            </select>
          </div>

          {/* Hierarchy Filters - Only show if subscription is selected */}
          {selectedSubscription && (
            <>
              <div>
                <label style={{
                  display: 'block',
                  fontSize: '14px',
                  fontWeight: '600',
                  color: '#374151',
                  marginBottom: '8px'
                }}>
                  📚 Subject
                </label>
                <select
                  value={filters.subjectId || ''}
                  onChange={(e) => {
                    const value = e.target.value;
                    handleFilterUpdate('subjectId', value ? parseInt(value) : undefined);
                    handleFilterUpdate('topicId', undefined);
                    handleFilterUpdate('moduleId', undefined);
                    handleFilterUpdate('chapterId', undefined);
                  }}
                  style={{
                    width: '100%',
                    padding: '10px 12px',
                    borderRadius: '8px',
                    border: '2px solid #e5e7eb',
                    fontSize: '14px',
                    color: '#374151',
                    background: 'white',
                    cursor: 'pointer'
                  }}
                >
                  <option value="">All Subjects</option>
                  {subjects.map(subject => (
                    <option key={subject.linkageId || subject.id} value={subject.linkageId || subject.id}>
                      {subject.subjectName || subject.name || 'Unknown Subject'}
                    </option>
                  ))}
                </select>
              </div>
            </>
          )}

          {filters.subjectId && topics.length > 0 && (
            <div>
              <label style={{
                display: 'block',
                fontSize: '14px',
                fontWeight: '600',
                color: '#374151',
                marginBottom: '8px'
              }}>
                📖 Topic
              </label>
              <select
                value={filters.topicId || ''}
                onChange={(e) => {
                  const value = e.target.value;
                  handleFilterUpdate('topicId', value ? parseInt(value) : undefined);
                  handleFilterUpdate('moduleId', undefined);
                  handleFilterUpdate('chapterId', undefined);
                }}
                style={{
                  width: '100%',
                  padding: '10px 12px',
                  borderRadius: '8px',
                  border: '2px solid #e5e7eb',
                  fontSize: '14px',
                  color: '#374151',
                  background: 'white',
                  cursor: 'pointer'
                }}
              >
                <option value="">All Topics</option>
                {topics.map(topic => (
                  <option key={topic.id} value={topic.id}>
                    {topic.topicName || topic.name || 'Unknown Topic'}
                  </option>
                ))}
              </select>
            </div>
          )}

          {filters.topicId && modules.length > 0 && (
            <div>
              <label style={{
                display: 'block',
                fontSize: '14px',
                fontWeight: '600',
                color: '#374151',
                marginBottom: '8px'
              }}>
                📚 Module
              </label>
              <select
                value={filters.moduleId || ''}
                onChange={(e) => {
                  const value = e.target.value;
                  handleFilterUpdate('moduleId', value ? parseInt(value) : undefined);
                  handleFilterUpdate('chapterId', undefined);
                }}
                style={{
                  width: '100%',
                  padding: '10px 12px',
                  borderRadius: '8px',
                  border: '2px solid #e5e7eb',
                  fontSize: '14px',
                  color: '#374151',
                  background: 'white',
                  cursor: 'pointer'
                }}
              >
                <option value="">All Modules</option>
                {modules.map(module => (
                  <option key={module.id} value={module.id}>
                    {module.moduleName || module.name || 'Unknown Module'}
                  </option>
                ))}
              </select>
            </div>
          )}

          {filters.moduleId && chapters.length > 0 && (
            <div>
              <label style={{
                display: 'block',
                fontSize: '14px',
                fontWeight: '600',
                color: '#374151',
                marginBottom: '8px'
              }}>
                📑 Chapter
              </label>
              <select
                value={filters.chapterId || ''}
                onChange={(e) => {
                  const value = e.target.value;
                  handleFilterUpdate('chapterId', value ? parseInt(value) : undefined);
                }}
                style={{
                  width: '100%',
                  padding: '10px 12px',
                  borderRadius: '8px',
                  border: '2px solid #e5e7eb',
                  fontSize: '14px',
                  color: '#374151',
                  background: 'white',
                  cursor: 'pointer'
                }}
              >
                <option value="">All Chapters</option>
                {chapters.map(chapter => (
                  <option key={chapter.id} value={chapter.id}>
                    {chapter.chapterName || chapter.name || 'Unknown Chapter'}
                  </option>
                ))}
              </select>
            </div>
          )}

          {/* Difficulty Level - Only show if subscription is selected */}
          {selectedSubscription && (
            <div>
              <label style={{
                display: 'block',
                fontSize: '14px',
                fontWeight: '600',
                color: '#374151',
                marginBottom: '8px'
              }}>
                📊 Difficulty Level
              </label>
              <div style={{ display: 'flex', gap: '8px', flexWrap: 'wrap' }}>
                {getDifficultyLevels().map(level => {
                  const isSelected = filters.difficultyLevel === level;
                  const colors = {
                    EASY: { bg: '#dcfce7', text: '#166534', selected: '#86efac' },
                    MEDIUM: { bg: '#fef3c7', text: '#92400e', selected: '#fcd34d' },
                    HARD: { bg: '#fee2e2', text: '#991b1b', selected: '#fca5a5' }
                  };
                  const color = colors[level];
                  
                  return (
                    <button
                      key={level}
                      onClick={() => handleFilterUpdate('difficultyLevel', isSelected ? undefined : level)}
                      style={{
                        padding: '8px 16px',
                        borderRadius: '8px',
                        border: isSelected ? `2px solid ${color.selected}` : `2px solid ${color.bg}`,
                        background: isSelected ? color.selected : color.bg,
                        color: color.text,
                        fontSize: '14px',
                        fontWeight: '600',
                        cursor: 'pointer',
                        transition: 'all 0.2s ease'
                      }}
                    >
                      {isSelected && '✓ '}{level}
                    </button>
                  );
                })}
              </div>
            </div>
          )}

          {/* Question Type - Only show if subscription is selected */}
          {selectedSubscription && (
            <div>
              <label style={{
                display: 'block',
                fontSize: '14px',
                fontWeight: '600',
                color: '#374151',
                marginBottom: '8px'
              }}>
                📝 Question Type
              </label>
              <select
                value={filters.questionType || ''}
                onChange={(e) => handleFilterUpdate('questionType', e.target.value || undefined)}
                style={{
                  width: '100%',
                  padding: '10px 12px',
                  borderRadius: '8px',
                  border: '2px solid #e5e7eb',
                  fontSize: '14px',
                  color: '#374151',
                  background: 'white',
                  cursor: 'pointer'
                }}
              >
                <option value="">All Types</option>
                {getQuestionTypes().map(type => (
                  <option key={type.value} value={type.value}>
                    {type.label}
                  </option>
                ))}
              </select>
            </div>
          )}

          {/* Action Buttons */}
          <div style={{ 
            display: 'flex', 
            gap: '12px',
            paddingTop: '16px',
            borderTop: '1px solid #e5e7eb'
          }}>
            <button
              onClick={onApplyFilters}
              disabled={!selectedSubscription}
              style={{
                flex: 1,
                padding: '12px 20px',
                borderRadius: '8px',
                border: 'none',
                background: selectedSubscription 
                  ? 'linear-gradient(135deg, #667eea 0%, #764ba2 100%)' 
                  : '#e5e7eb',
                color: selectedSubscription ? 'white' : '#9ca3af',
                fontSize: '14px',
                fontWeight: '600',
                cursor: selectedSubscription ? 'pointer' : 'not-allowed',
                transition: 'transform 0.2s ease',
                opacity: selectedSubscription ? 1 : 0.6
              }}
              onMouseOver={(e) => {
                if (selectedSubscription) {
                  e.currentTarget.style.transform = 'translateY(-2px)';
                }
              }}
              onMouseOut={(e) => {
                if (selectedSubscription) {
                  e.currentTarget.style.transform = 'translateY(0)';
                }
              }}
            >
              Apply Filters
            </button>
            <button
              onClick={() => {
                onClearFilters();
                // Reset to first subscription
                if (subscriptions.length > 0) {
                  setSelectedSubscription(subscriptions[0]);
                }
              }}
              style={{
                flex: 1,
                padding: '12px 20px',
                borderRadius: '8px',
                border: '2px solid #e5e7eb',
                background: 'white',
                color: '#6b7280',
                fontSize: '14px',
                fontWeight: '600',
                cursor: 'pointer',
                transition: 'all 0.2s ease'
              }}
              onMouseOver={(e) => {
                e.currentTarget.style.background = '#f3f4f6';
                e.currentTarget.style.borderColor = '#d1d5db';
              }}
              onMouseOut={(e) => {
                e.currentTarget.style.background = 'white';
                e.currentTarget.style.borderColor = '#e5e7eb';
              }}
            >
              Clear Filters
            </button>
          </div>
        </div>
      )}
    </div>
  );
};

export default StudentQuestionFilters;

